import React from "react";
import styled from "./Footer.module.scss";
import classNames from "classnames/bind";
import CheckIcon from '@mui/icons-material/Check';
function Footer() {
  const cx = classNames.bind(styled);
  return (
    <div>
      <div className={cx("Footer")}>
        <div className="container">
            <div className={cx("flex")}>
              <div className={cx("Footer-content-flex")}>
                <div className={cx("Footer-content")}>
                  <div className={cx("Footer-content-title")}>
                    <h3>HỖ TRỢ DỊCH VỤ</h3>
                  </div>
                  <div className={cx("Footer-content-help")}>
                    <ul>
                        <li>Tổng Đài CSKH, Báo Hỏng Wifi: <strong>1800.8119</strong></li>
                        <li>Tổng Đài CSKH, Dịch Vụ Di Động: <strong>1800.8098</strong></li>
                        <li>Lắp Đặt Nhanh Trong Vòng 24h-48h</li>
                    </ul>
                  </div>
                </div>
                <div className={cx("Footer-service")}>
                  <div className={cx("Footer-service-title")}>
                    <h3>DỊCH VỤ LIÊN KẾT KHÁC</h3>
                  </div>
                  <div className={cx("Footer-service-other")}>
                    <ul>
                        <li>
                            <CheckIcon/>
                            <p>Lắp mạng Viettel 63 tỉnh</p>
                            <div>New</div>
                        </li>
                        <li>
                            <CheckIcon/>
                            <p>Chữ Kí Số ViettelCA</p>
                            <div>New</div>
                        </li>
                        <li>
                            <CheckIcon/>
                            <p>Di Động Trả Sau</p>
                            <div>New</div>
                        </li>
                        <li>
                            <CheckIcon/>
                            <p>Đăng Kí 4G Viettel</p>
                            <div>New</div>
                        </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      <div className={cx("Footer-title")}>Copyright 2023 Vietteltelecom</div>
    </div>
  );
}

export default Footer;
