import PhoneCallbackIcon from '@mui/icons-material/PhoneCallback';
import React from 'react';
import './App.scss';
import img from '../src/Image/zaloicon4.png'

function PhoneIconBottom() {

    
    return (  
        <div>
            <div  className='PhoneIconBottom'>
                <div className='PhoneBottomIcon' >
                    <PhoneCallbackIcon className='IconPhoneBottom Phone'/>
                </div>
                <div className='phoneNumber'><a href="tel:0978070070" className={'PhoneNumberHide PhoneNumberShow'}>0978.070.070</a></div>
            </div>
            <div  className='ZaloIconBottom'>
                <div className='zaloBottomIcon'>
                    <a href="https://zalo.me/0978070070"> 
                        <img src={img} alt="" />
                    </a>
                </div>
            </div>
        </div>
        
        
    );
}

export default PhoneIconBottom;