import React from "react";
import styled from "./TheViettel.module.scss";
import classNames from "classnames/bind";
import LocalPhoneIcon from "@mui/icons-material/LocalPhone";
import img1 from '../../Image/WifiImg.jpg'
import img2 from '../../Image/4GImg.jpg'

function TheViettel() {
  const cx = classNames.bind(styled);
  return (
    <div className={cx("theViettel")}>
      <div className="container">
        <div className={cx("content")}>
          <div className={cx("title")}>
            <h2>TỔNG ĐÀI VIETTEL TÂY NINH</h2>
          </div>
          <div className={cx("content-silde")}>
              <div className={cx("silde1")}>
                <img src={img1} alt="" />
                <div className={cx("silde1-content")}>
                  <h3>ĐĂNG KÍ LẮP ĐẶT WIFI</h3>
                  <p>Miễn phí lắp đặt - Miễn phí moderm wifi 5G</p>
                  <div className={cx("silde1-content-hotline")}>
                    <a href="/#">
                      <LocalPhoneIcon />
                      <p>0978070070</p>
                    </a>
                  </div>
                </div>
              </div>
              <div className={cx("silde2")}>
                <img src={img2} alt="" />
                <div className={cx("silde2-content")}>
                  <h4>ĐĂNG KÍ 4G VIETTEL</h4>
                  <p>Gói data 4G và gói nội mạng - ngoại mạng</p>
                  <div className={cx("silde2-content-hotline")}>
                    <a href="/#">
                      {/* <LocalPhoneIcon /> */}
                      <p>Đăng Kí</p>
                    </a>
                  </div>
                </div>
              </div>
            </div>
        </div>
      </div>
    </div>
  );
}
export default TheViettel;
